import React from 'react'
import { NavLink } from 'react-router-dom'
 export default function renderNavigation({playlist}) {
    return (
      <nav className="navbar navbar-expand-lg navbar-light bg-light">
        <a className="navbar-brand" href="/"> üéßFindMusic</a>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item">
              <NavLink className='nav-link' activeClassName='active' to="/" exact>Welcome</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className='nav-link' activeClassName='active' to="/tracks">Find Tracks</NavLink>
            </li>           
            <li className="nav-item">
              <NavLink className='nav-link' activeClassName='active' to="/playlist">My Playlist ({playlist.length})</NavLink>
            </li>           
          </ul>
        </div>
      </nav>
    );
  }